.hovereffect {
    width:100%;
    /* height:100%; */
    /* float:left; */
    overflow:hidden;
    position:relative;
    text-align:center;
    cursor:pointer;
    border: 3px solid #fff;
    margin-bottom: 20px;
    /* display: inline-block;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    flex: 0 0 240px;
    background-color: #333;
    overflow: hidden; */
    border-radius: 20px;
    box-shadow:
      rgba(black, 0.66) 0 30px 60px 0,
      inset rgb(150, 145, 145) 0 0 0 5px,
      inset rgba(white, 0.5) 0 0 0 6px;
}
.movieimg{
    width: 100%;
}
.hovereffect .overlay {
    width:100%;
    height:100%;
    position:absolute;
    overflow:hidden;
    top:0;
    left:0;
    opacity:0;
    background-color:rgba(0,0,0,0.5);
    transition:all .4s ease-in-out
    }
    
.hovereffect img {
    display:block;
    position:relative;
    transition:all .4s linear;
    width:100%;
    }
    
.hovereffect h2 {
    text-transform:uppercase;
    color:#fff;
    text-align:center;
    position:relative;
    font-size:18px;
    background:rgba(0,0,0,0.7);
    transform:translatey(-100px);
    transition:all .2s ease-in-out;
    padding:10px;
    font-weight: bolder;
}
.hovereffect span {
    color: rgb(95, 228, 95);
    font-size:13px;
    position:relative;
    padding:15px;
    text-align:center;
    position:relative;

} 
.hovereffect:hover{
    border: 3px solid rgb(95, 228, 95);

}     
.hovereffect:hover img {
    transform:scale(1.2);
 }
    
.hovereffect:hover .overlay {
    opacity:1;
    filter:alpha(opacity=200);
    }
    
 .hovereffect:hover h2 {
    opacity:1;
    filter:alpha(opacity=100);
    transform:translatey(0);
}
