.rmdb-searchbar {
    width: 100%;
    height: 105px;
    /* background: #1c1c1c; */
    position: relative;
    padding: 25px 20px 0px 20px;
    box-sizing: border-box;
    color: #fff;
  }
  
  .rmdb-searchbar-content {
    max-width: 1280px;
    width: 100%;
    height: 55px;
    background:#000;
    margin: 0 auto;
    border-radius: 40px;
    border: 2px solid rgb(95, 228, 95);
    position: relative;
    color: #fff;
  }
  
  .rmdb-fa-search {
    position: absolute;
    left: 20px;
    top: 10px;
    color: rgb(95, 228, 95);
  }
  
  .rmdb-searchbar-input {
    /* font-family: 'Abel', sans-serif; */
    font-size: 25px;
    position: absolute;
    left: 70px;
    top: 7px;
    border: 0;
    background: transparent;
    height: 40px;
    color: #fff;
  }
  
  .rmdb-searchbar-input:focus {
    outline: none;
  }
  
  @media screen and (max-width: 720px) {
    .rmdb-searchbar-input {
      font-size: 18px;
      color: #fff;
    }
  }
.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid rgb(95, 228, 95); 
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 20px auto;
}

@-webkit-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.movieinfo {
    background-size: cover !important;
    background-position: center !important;
    width: 100%;
    height: 100%;
    padding: 40px 20px;
    box-sizing: border-box;
    -webkit-animation: animateMovieinfo 1s;
            animation: animateMovieinfo 1s;
  }
  
  @-webkit-keyframes animateMovieinfo {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
  }
  
  @keyframes animateMovieinfo {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
  }
  
  .movieinfo-content {
    /* max-width: 1280px; */
